import React, { useRef, useState } from 'react';
import { Col, Container, Row, Table } from 'react-bootstrap'
import { useNavigate, useParams } from 'react-router-dom';
import { Autoplay } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';

// import required modules
import { Pagination } from 'swiper/modules';

const Pick = (props) => {
	let navigate = useNavigate();

	let { pickDetail } = props;
	let { id } = useParams();
	let selproduct = pickDetail.find((x) => x.id == id);

	return (

		<div className='detail_all'>

			<Container>
				<div className='d_all'>
					<div className='d_img'>

						<Row>
							<Col className='text-center'>
								<img src={process.env.PUBLIC_URL + selproduct.imgUrl} style={{ width: '75vmin', paddingBottom: '20px' }} alt="" />
							</Col>

							<Col>
								<div className='d_text text-center'>

									<div className='pt-5 mb-2' style={{ border: '1px solid #f3a914', borderRadius: '100px', padding: '0px 20px' }}>
										<h2 className='mb-4 text-center' style={{ fontSize: '6vmin', letterSpacing: '5px' }}>{selproduct.title}</h2>
										<h2 style={{ fontSize: '2.5vmin' }}># {selproduct.h2}</h2>
										<h2 style={{ fontSize: '2.5vmin', paddingBottom: '20px' }}># {selproduct.p}<br /><br /></h2>
									</div>

									<div className='pt-4 pb-4' style={{ backgroundColor: '#f3a914', borderRadius: '100px', marginBottom: '100px' }}>
										<h4>{selproduct.single}</h4>
										<h4>{selproduct.kcal}</h4>
										<h4>{selproduct.mg}</h4>
										<h4>{selproduct.sacchar}</h4>
										<h4>{selproduct.fat}</h4>
										<h4>{selproduct.protein}</h4>
									</div>

								</div>
							</Col>
						</Row>
						{/* <img src={process.env.PUBLIC_URL + "/img/note.png"} style = {{ width: '700px', marginBottom : '70px', marginTop: '30px'}} alt="" /> */}
					</div>


					<img src={process.env.PUBLIC_URL + "/img/line.png"} style={{ marginBottom: '70px', marginTop: '30px' }} alt="" />

				</div>

			</Container>

			<div className='pick'>
				<h3 className='text-center'>MD's Pick</h3>
				<Swiper
					style={{
						"--swiper-pagination-color": "#f3a914",
						"--swiper-pagination-bullet-inactive-color": "#645845",
						"--swiper-pagination-bullet-inactive-opacity": "1",
						"--swiper-pagination-bullet-size": "10px",
						"--swiper-pagination-bullet-horizontal-gap": "6px",
						height: '550px',
						marginTop: '50px',
						marginBottom: '50px',
						display: 'flex',
						justifyContent: 'center',
						alignItems: 'center'
					}}
					slidesPerView={4}
					spaceBetween={30}
					loop={true}
					loopAdditionalSlides={1}
					pagination={{
						clickable: true,
					}}
					autoplay={{
						delay: 1000,
						// disableOnInteraction: true,
					}}
					modules={[Pagination]}
					breakpoints={{
						100: {
							slidesPerView: 1,
						},
						648: {
							slidesPerView: 2,
						},
						950: {
							slidesPerView: 3,
						},
						1280: {
							slidesPerView: 4
						}

					}}
					className="mySwiper"

				>
					{
						pickDetail.map((val, i) => {
							return (
								<SwiperSlide>
									<div style={{
										display: 'flex',
										justifyContent: 'center',
										alignItems: 'center',
										flexDirection: 'column',
										height: '500px',
										border: '1px solid #5e4e46'
									}}
										onClick={() => { navigate('/detail/pick/' + [i]) }}>
										<img src={process.env.PUBLIC_URL + val.imgUrl} style={{ width: '280px', height: '300px' }} className='p_hover pb-3' />
										<span className='mb-2' style={{ color: 'rgb(251, 178, 33)' }}>{val.h2}</span>
										<h5 style={{ fontSize: '1.5rem', color: '#5e4e46' }}> {val.title}</h5>
										<p style={{ opacity: '0.3', padding: '0px 20px', color: '#5e4e46' }} className='text-center mt-2'>{val.p}</p>
									</div>
								</SwiperSlide>
							)

						})
					}
				</Swiper>
			</div>

		</div>


	)
}

export default Pick