import React from 'react'
import { useNavigate } from 'react-router-dom';

const Side = (props) => {

    // let [side, setSide] = useState(data3);
    let { side, setSide } = props;
    let navigate = useNavigate();

    return (
        <div className="all">
            
            {
                side.map((val, i) => {
                    return (
                        <div className='m_side'  onClick={()=>{navigate('/detail/side/' + [i])}}>
                            <img src={process.env.PUBLIC_URL+ val.imgUrl} />
                            <span className='mb-2' style={{color: '#f2A900'}}>{val.h2}</span>
                            <h5 style = {{fontSize: '1.5rem'}}> {val.title}</h5>
                            <p style={{opacity: '0.5', padding: '0px 20px', color:'#5C4738' }} className='text-center mt-2'>{val.p}</p>
                        </div>
                    )
                })
            }
           
        </div>
    )
}

export default Side