import React, { useRef, useState } from 'react';
import { Carousel, Button } from 'react-bootstrap';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from 'swiper/modules';
import { Link, useNavigate } from 'react-router-dom';

// Import bootstrap styles
import 'bootstrap/dist/css/bootstrap.min.css';
//
import styled from 'styled-components'

import 'swiper/css';
import 'swiper/css/pagination';

import Burger from './Burger'
import Chicken from './Chicken'
import Side from './Side'

let SlideText = styled.div`
width: 80%;
text-align: left;
margin-bottom: 13%;
`

const Home = (props) => {
	let { burger, setBurger, chicken, setChicken, side, setSide } = props;

	const swiperSlide = useRef();
	//addclass removeclass
	let [count, setCount] = useState(1)
	let [tab, setTab] = useState(0)
	let [sns, setSns] = useState([process.env.PUBLIC_URL + "/img/sns1.png", process.env.PUBLIC_URL + "/img/sns2.png", process.env.PUBLIC_URL + "/img/sns3.png", process.env.PUBLIC_URL + "/img/sns4.png", process.env.PUBLIC_URL + "/img/sns5.png"]);
	let navigate = useNavigate();

	return (
		<div>
			<div className='fixed_img'> </div>

			<div className="slideWrap">
				<Carousel>

					<Carousel.Item interval={3000}>
						<img className="s_img" src={process.env.PUBLIC_URL + "/img/slide1.jpg"} alt="" style={{width: '100vw'}}/>
						<Carousel.Caption>
							<SlideText>
								<p style={{ fontSize: '2.5vw', marginBottom: '4%', color: '#e3d1aa' }}>Golden Mac and cheese Chicken Burger</p>
								<h3 style={{ fontSize: '4.8vw', marginBottom: '8%', color: '#f9f5e0' }}>풍미가득 순 쇠고기 패티와 <br /> 골든맥앤치즈의 만남!</h3>
								<h4 style={{ fontSize: '3vw', color: '#e3d1aa' }}>" 골든맥앤치즈치킨버거 "</h4>
							</SlideText>
						</Carousel.Caption>
					</Carousel.Item>

					<Carousel.Item interval={3000}>
						<img className="s_img" src={process.env.PUBLIC_URL + "/img/slide2.jpg"} alt=""  style={{width: '100vw'}}/>
						<Carousel.Caption>
							<SlideText>
								<p style={{ fontSize: '2.5vw', marginBottom: '4%', color: '#e3d1aa' }}>Golden Mac and Cheese Beef Burder</p>
								<h3 style={{ fontSize: '4.8vw', marginBottom: '8%', color: '#f9f5e0' }}>두툼한 싸이패티와 <br /> 골든맥앤치즈의 만남!</h3>
								<h4 style={{ fontSize: '3vw', color: '#e3d1aa' }}>" 골든맥앤치즈비프버거 "</h4>
							</SlideText>
						</Carousel.Caption>
					</Carousel.Item>

					<Carousel.Item interval={3000}>
						<img className="s_img" src={process.env.PUBLIC_URL + "/img/slide3.jpg"} alt=""  style={{width: '100vw'}}/>
						<Carousel.Caption>
							<SlideText>
								<p style={{ fontSize: '2.5vw', marginBottom: '4%', color: '#e3d1aa', width:'600px'}}>Honeny Twist Snack Thigh Boneless Chicken</p>
								<h3 style={{ fontSize: '4.5vw', marginBottom: '8%', color: '#f9f5e0' }}>바삭한 꿀꽈배기와 겉바속촉 싸이순살의 환상적인 콜라보!</h3>
								<h4 style={{ fontSize: '3vw', color: '#e3d1aa' }}>" 꿀꽈배기싸이순살 "</h4>
							</SlideText>
						</Carousel.Caption>
					</Carousel.Item>

				</Carousel>
			</div>

			<div className="new">
				<Swiper
					direction={'vertical'}
					slidesPerView={1}
					spaceBetween={0}
					centeredSlides={true}
					mousewheel={false}
					autoplay={{
						delay: 3000,
						disableOnInteraction: false,
					}}
					loop={true}
					loopAdditionalSlides={1}
					modules={[Autoplay]}
					className="mySwiper"
					ref={swiperSlide}
					style={{ height: '80px', 
						paddingTop: '10px', 
						transform:'translate(0, -20px)', 
						fontFamily: 'Cafe24Ssurround' 
					}}
				>
					<SwiperSlide>
						<h2 style={{ fontSize: '3.5vmin'}}>* HIT2 X 맘스터치 게임쿠폰 100% 지급 *</h2>
					</SwiperSlide >

					<SwiperSlide>
						<h2 style={{ fontSize: '3.5vmin'}}>* 선착순 20만명 싸이버거 단품무료 * </h2>
					</SwiperSlide>

					<SwiperSlide>
						<h2 style={{ fontSize: '3.5vmin'}}>* 행사제품 구매시, 동일제품 쿠폰 증정 *</h2>
					</SwiperSlide>
				</Swiper>
			</div>



			<div className="menu">
				<h3>MOM <span className='c_y'>'</span>S <span className='c_y'>TOUCH</span> MENU</h3>
				<Link className = 'mt-3' style = {{ color: '#fbb221', fontSize: '1.3rem'}}>더 많은 정보 보러가기</Link>

				<div className="m_buttons">
					<Button variant="warning" className="m_button" onClick={() => { 
						setTab(0)
						setCount(1)
					}}>BUGER</Button>{' '}

					<Button variant="warning" className="m_button" onClick={() => { 
						setTab(1)
						setCount(1)
					}}>CHICKEN</Button>{' '}

					<Button variant="warning" className="m_button" onClick={() => { 
						setTab(2)
						setCount(1)
					}}>SIDE / SNACK</Button>{' '}
				</div>

				<div>
					{
						((key, i) => {
							if (tab === 0) {
								return <Burger 
									count={count} setCount={setCount} 
									burger={burger} setBurger={setBurger} key = {i} />
							} else if (tab === 1) {
								return <Chicken 
									count={count} setCount={setCount} 
									chicken={chicken} setChicken={setChicken} key = {i} />
							} else if (tab === 2) {
								return <Side 
									count={count} setCount={setCount} 
									side={side} setSide={setSide} key = {i}/>
							}
						})()
					}

				</div>
			</div>


			<div className="banner">
				<span><img className="s_img" src={process.env.PUBLIC_URL + "/img/FRANCHISE.png"} alt="" /></span>
				<div className='b_textW'>
					<p>맘스터치는 가맹점 사장님의 성공을 위해 먼저 무엇이 필요한지 고민합니다.</p>
					<h2>맘스터치와 함께 할 가맹점 점주님을 모십니다</h2>
					<button>가맹점 문의하기</button>
				</div>
			</div>

			<div className="update">
				<p>* MOM'S NEWS *</p>
				<h3>2022-10-27</h3>

				<h2>제품 영양성분 정보 / 2023년 9월 23일 업데이트</h2>
				<span></span>
			</div>

			<div className="sns">
				{
					sns.map((val, i) => {
						return (
							<div className="s_img" >
								<a href="https://www.instagram.com/momstouch.love/"><img src={sns[i]} alt=""  style={{ width: '100%'}} /></a>
							</div>
						)
					})
				}

			</div>

			
		</div>

	)
}

export default Home